
body {
  font-family: sans-serif;
  font-size: 12px;
  background: #eee;
}
html {
  background: #eee;
}
* {
  margin: 0;
  padding: 0;
}
ul, li {
  list-style: none;
}
img {
  border-style: none;
}
audio {
  display: block;
}
audio:not([controls]) {
  display: block;
}
input {
  border-style: none;
  border-radius: .1rem;
  padding: 0;
}
input:hover {
    border-style: none;
}
button, input, select, textarea {
  font-size: 100%;
  outline: 0;
}
button, input {
  overflow: visible;
}
input, textarea, button, a {
  -webkit-tap-highlight-color: transparent;
}
@media screen and (min-width: 414px) {
html {
    font-size: 55.2px;
}
}
@media screen and (min-width: 375px) and (max-width: 413px) {
html {
    font-size: 50px;
}
}
@media screen and (min-width: 320px) and (max-width: 374px) {
html {
    font-size: 42.6667px;
}
}
.search {
  background: #fff;
  width: 7.1rem;
  height: 0.8rem;
  padding: 0.2rem;
}
.search input {
    width: 100%;
    padding-left: 5%;
    height: 100%;
    background: #eee;
    line-height: 0.3rem;
    font-size: 0.25rem;
}
.search .cancelBtn {
    display: none;
    font-size: 0.31rem;
    padding-left: 0.1rem;
}
.search .visible {
    display: inline;
}
.search .focus {
  width: 88%;
}
.searchList {
  width: 6.9rem;
  padding: 0 0.3rem 0 0.3rem;
  z-index: 50;
  position: absolute;
  top: 1.2rem;
  left: 0;
  background: #fff;
}
.searchList li {
    width: 6.9rem;
    font-size: 0.31rem;
}
.searchList li a {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: justify;
          -ms-flex-pack: justify;
              justify-content: space-between;
      color: #000;
      text-decoration: none;
      padding: 0.2rem 0 0.2rem 0;
}
.swiper-container {
  width: 7.5rem;
  height: 3rem;
  overflow: hidden;
}
.swiper-container .swiper-slide {
    width: 7.5rem;
}
.swiper-container .swiper-slide img {
      width: 100%;
      height: 100%;
}
.controler-bar {
  width: 7.5rem;
  height: 1rem;
  position: fixed;
  bottom: 0;
  left: 0;
  background: -webkit-linear-gradient(top, #f9f9f9, #f3f3f3);
}
.controler-bar .controler-barImg {
    float: left;
    width: 0.7rem;
    height: 0.7rem;
    margin-top: 0.15rem;
    margin-left: 0.3rem;
    border-radius: 0.1rem;
}
.controler-bar .controler-barImg img {
      width: 100%;
      height: 100%;
      border-radius: 0.1rem;
}
.controler-bar .songName {
    float: left;
    height: 0.42rem;
    padding-left: 0.2rem;
    padding-top: 0.29rem;
    font-size: 0.3rem;
}
.controler-bar .controler-barBtn {
    float: right;
    padding-right: 0.3rem;
    width: 0.4rem;
    height: 0.4rem;
    padding-top: 0.3rem;
}
.controler-bar .controler-barBtn img {
      width: 100%;
      height: 100%;
}
.tab-navList {
  width: 7.5rem;
  background: #fff;
  margin-top: 0.2rem;
}
.tab-navList p {
    font-size: 0.25rem;
}
.tab-navList a {
    text-decoration: none;
}
.tab-navList .song-sheet {
    text-align: center;
    height: 0.6rem;
    width: 7.5rem;
    line-height: 0.6rem;
    font-size: 0.3rem;
    color: #666;
}
.tab-navList li {
    width: 3.75rem;
    float: left;
    background: #fff;
    padding-top: 0.05rem;
    padding-bottom: 0.15rem;
}
.tab-navList li img {
      height: 3.75rem;
}
.tab-navList li .title {
      width: 3.35rem;
      line-height: 0.5rem;
      font-size: 0.27rem;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      color: #333;
      padding-left: 0.2rem;
      padding-right: 0.2rem;
}
.tab-navList li .author {
      color: #333;
      line-height: 0.3rem;
      padding-left: 0.2rem;
      padding-right: 0.2rem;
}
.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 7.5rem;
  height: 1rem;
  line-height: 1rem;
  color: #fff;
  font-size: 0.33rem;
  padding-left: 0.2rem;
  background: rgba(178, 83, 122, 0);
}
.singerPhoto {
  width: 7.5rem;
  height: 7.5rem;
}
.singerPhoto img {
    width: 100%;
    height: 100%;
}
.sheetTitle {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 7.3rem;
  height: 2.3rem;
  padding: 0.1rem;
  position: absolute;
  top: 55%;
  left: 0;
  margin-top: -1.5rem;
  background: -webkit-linear-gradient(top, rgba(145, 146, 225, 0), #9192e1);
}
.sheetTitle .sheetTitle_left {
    width: 6.5rem;
    color: #fff;
    line-height: 0.8rem;
}
.sheetTitle .sheetTitle_left h1 {
      font-size: 0.65rem;
}
.sheetTitle .sheetTitle_left p {
      font-size: 0.4rem;
}
.sheetTitle .sheetTitle_right {
    margin-top: 0.25rem;
    width: 0.5rem;
    height: 1rem;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.43);
    border: 1px solid #eee;
}
.sheetTitle .sheetTitle_right .sheetTitle_inner {
      width: 0.5rem;
      height: 0.5rem;
      padding-top: 0.25rem;
}
.sheetTitle .sheetTitle_right .sheetTitle_inner img {
        width: 100%;
        height: 100%;
}
.sheetContent {
  width: 7.3rem;
  padding-left: 0.2rem;
  background: #b2537a;
  margin-top: 0.02rem;
}
.sheetContent .contentItem {
    width: 7.3rem;
    height: 1.2rem;
}
.sheetContent .contentItem .content_left {
      float: left;
      width: 6.6rem;
      height: 0.88rem;
      padding: 0.16rem 0 0.16rem 0;
}
.sheetContent .contentItem .content_left audio {
        width: 6.6rem;
        height: 0.56rem;
}
.sheetContent .contentItem .content_left h2 {
        color: #fff;
        font-weight: 400;
}
.sheetContent .contentItem .content_left p {
        color: #aaa;
        font-size: 0.25rem;
}
.sheetContent .contentItem .content_right {
      float: left;
      width: 0.6rem;
      height: 0.5rem;
      padding-top: 0.35rem;
}
.sheetContent .contentItem .content_right span {
        display: inline-block;
        width: 0.1rem;
        height: 0.1rem;
        margin-right: 0.1rem;
        border-radius: 50%;
        background: #000;
}
.space {
  width: 7.5rem;
  height: 1rem;
}
.shadow {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 1.2rem;
  left: 0;
  background: #333;
  opacity: .8;
  z-index: 20;
}
.songSheetContainer {
  width: 7.1rem;
  margin: 0.2rem;
  border-radius: 0.1rem;
}
.songSheetContainer .songSheetItem {
    width: 7.1rem;
    height: 2rem;
    margin-bottom: 0.2rem;
    background: #fff;
    color: #000;
}
.songSheetContainer .songSheetItem .imgContainer {
      width: 2rem;
      height: 2rem;
      float: left;
      background: green;
      border-bottom-left-radius: 0.1rem;
      border-top-left-radius: 0.1rem;
}
.songSheetContainer .songSheetItem .imgContainer img {
        width: 100%;
        height: 100%;
        border-bottom-left-radius: 0.1rem;
        border-top-left-radius: 0.1rem;
}
.songSheetContainer .songSheetItem .infoList {
      width: 4.9rem;
      height: 2rem;
      padding: 0.1rem;
      float: left;
}
.songSheetContainer .songSheetItem .infoList h3 {
        text-align: left;
        font-size: 0.3rem;
        font-weight: 500;
}
.songSheetContainer .songSheetItem .infoList li {
        text-align: left;
        font-size: 0.25rem;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        line-height: 0.45rem;
}
.songSheetContainer .songSheetItem .infoList li p {
          color: #666;
          display: inline;
}
.tab-nav {
  width: 7.5rem;
}
.tab-title {
  width: 7.5rem;
  height: 0.8rem;
  background: #fff;
}
.tab-title a {
    float: left;
    width: 3.75rem;
    height: 0.8rem;
    line-height: 0.8rem;
    text-align: center;
    font-size: 0.3rem;
    color: #333;
    text-decoration: none;
}
.router-link-active {
  color: #1f5508;
  background: #ccc;
  opacity: .5;
}
.tabs {
  width: 7.5rem;
}



